.main {
    padding-top: 80px;
    padding-bottom: 80px;

    display: flex;
    justify-content: center;
}

.apply {
    background-color: #343434;
    border-radius: 33px;
    display: flex;
    width: calc(100% * (7/9));
    height: 370px;

    .left {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 94px;

        .title {
            padding-left: 10px;
            padding-right: 10px;
            color : #ffffff;
            font-size: 52px;
            white-space: pre-line;
        }

        .btn {
            width: fit-content;
            padding-left: 40px;
            padding-right: 40px;
            padding-top: 30px;
            padding-bottom: 30px;
            color : #ffffff;
            font-size: 24px;
            background-color: #486CE5;
            border-radius: 6px;
            cursor:pointer;
        }
    }

    .right {
        flex: 1;
    }
}

.h37 {
    height: 37px;
}